@mixin home-image($url, $zIndex) {
  position: absolute;
  height: 100%;
  width: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-image: url($url);
  z-index: $zIndex;
  pointer-events: none; // Allows mouse events to pass through the element
  filter: brightness(0.5);
}
.home {
  height: 100vh;
  width: 100vw;
}
.home-image {
  @include home-image('../../assets/images/cover-1.png', 3);
}
.home-image-overlay {
  @include home-image('../../assets/images/cover.jpg', 1);
}

.home-content {
  display: grid;
  grid-template-rows: 55% 35% 10%;
  justify-content: center;
  height: 100vh;
}

.text-container {
  width: fit-content;
  align-self: end;
  grid-row: 1/2;
  color: #fff;
  justify-self: center;
  z-index: 5;
  text-align: center;
  font-family: 'Roboto', sans-serif;

  span {
    font-size: 10vmin;
  }
  p {
    font-size: 2.5vmin;
  }
  .job-title {
    font-size: 3vmin;
  }
}

.projects-arrow {
  grid-row: 3/4;
  position: relative;
  height: 10vh;
  color: #fff;
  text-align: center;
  font-size: 2.5vmin;
  z-index: 5;
  overflow: hidden;
  .arrow {
    position: absolute;
    height: 100%;
    top: 0;
    width: 1.7vmin;
    transform: rotate(90deg);
    animation: slideDown 1s ease-in-out infinite;
    left: 50%;
    @keyframes slideDown {
      0%,
      100% {
        transform: translate(-50%, 0);
      }
      50% {
        transform: translate(-50%, 10px);
      }
    }
  }
  @media (min-width: 1025px) {
    &:hover {
      filter: brightness(2);
      .arrow {
        animation: none;
        left: 50%;
        transform: translate(-50%, 10px);
      }
    }
  }
}
@media (max-width: 1023px) {
  .text-container {
    width: 70vw;
  }
}

@media (max-width: 700px) {
  .home-content {
    grid-template-rows: 55% 25% 20%;
  }
  .text-container {
    width: 70vw;
    span {
      font-size: 12vmin;
    }
    p {
      font-size: 3.3vmin;
    }
  }
  .projects-arrow {
    height: 10vh;
    font-size: 4.5vmin;
    .arrow {
      width: 3vmin;
    }
  }
}

@media (max-height: 600px) and (orientation: landscape) {
  .home-content {
    display: grid;
    grid-template-rows: 35% 35% 30%;
    justify-content: center;
    height: 100vh;
  }

  .text-container {
    width: 35vw;
    align-self: end;
    grid-row: 1/2;
    color: #fff;
    justify-self: center;
    z-index: 5;
    text-align: center;
    span {
      font-size: 12vmin;
    }
    p {
      font-size: 3vmin;
    }
  }
  .projects-arrow {
    grid-row: 3/4;
    align-self: start;
    position: relative;
    height: 10vh;
    color: #fff;
    text-align: center;
    font-size: 2.5vmin;
    z-index: 5;
    overflow: hidden;
    .arrow {
      position: absolute;
      height: 100%;
      top: 0;
      width: 1.7vmin;
      transform: rotate(90deg);
      animation: slideDown 1s ease-in-out infinite;
      left: 50%;
    }
  }
}
