.info-menu {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(2, 1fr);
  grid-row: 2/3;
  z-index: 5;
  padding: 10% 0;
}

.info-icons-container {
  width: 100%;
  grid-row: 1/2;
  display: flex;
  justify-content: space-between;
}

.info-icons {
  height: 5vmin;
  cursor: pointer;
  &:hover {
    filter: brightness(0.5);
  }
}

.info-link {
  grid-row: 2/3;
  display: grid;
  grid-template-columns: 90% 10%;
  grid-template-rows: 100%;
  background-color: #0005;
  border-radius: 1vmin;
  height: 5vmin;
  width: 80%;
  justify-self: center;
  line-height: 5vmin;
  font-size: 3vmin;
  text-align: center;
  color: #fff;
  span {
    grid-column: 1/2;
  }
  a {
    display: block;
    align-self: center;
    grid-column: 2/3;
    height: 100%;
    line-height: 180%;
  }
  .info-link-icon {
    height: 50%;
    max-width: 3vw;
    &:hover {
      filter: brightness(0.5);
    }
  }
}

@media (max-width: 700px) {
  .info-icons {
    height: 8vmin;
    cursor: pointer;
    &:hover {
      filter: brightness(1);
    }
  }
  .info-link {
    height: 10vmin;
    line-height: 10vmin;
    font-size: 5vmin;
    a {
      display: block;
      align-self: center;
      grid-column: 2/3;
      height: 100%;
      line-height: 180%;
    }
    .info-link-icon {
      height: 100%;
      max-width: 5vw;
      padding-right: 100%;
      &:hover {
        filter: brightness(0.5);
      }
    }
  }
}

@media (max-height: 600px) and (orientation: landscape) {
  .info-link {
    height: 5vmin;
    width: 60%;
    align-self: end;
    line-height: 5vmin;
    font-size: 3vmin;
    .info-link-icon {
      height: 50%;
      max-width: 3vw;
    }
  }
}
